.col-xs-4{
  margin: 0;
  padding-right: 0;
  padding-left: 0;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btnInactivo{
  display: inline-block;
  background: white;
  color: grey;
  text-transform: uppercase;
  width: 100%;
  height: 40px;
  outline: none;
  border: none;
  margin: 0;
  border-right: 1px solid #fff;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btnActivo{
  display: inline-block;
  text-transform: uppercase;
  width: 100%;
  height: 40px;
  outline: none;
  border: none;
  margin: 0;
  background: orange;
  color: white;
  -webkit-box-shadow: 0px -4px 19px 0px rgba(128, 125, 118, 0.53);
  box-shadow: 0px -4px 19px 0px rgba(128, 125, 118, 0.53);
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.main{
  padding: 0;
}

.hidden{
  display: none;
}

.row.mini-nav {
  border-bottom: 1px solid orange;
  margin-bottom: 15px;
}

.row.mini-nav button:hover {
  color: orange;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.row.mini-nav button {
  font-weight: 500;
}

.row.mini-nav .btnActivo:hover {
  color: white;
}


